<template>
 <div class="wrapbox">

  <div class="header">
          <slider>
                <div class="swiper-slide" v-for="item in slides">
                  <div class="img">
                       <img :src="item.src">
                  </div>
                </div>
          </slider>
         <router-link :to="{name:'hospital_appoint'}">
             <div class="halo">
              <div class="appoint_box">
                  <i class="iconfont icon-yachi"></i>
                  <p>预约看牙</p>
              </div> 
              <div class="pulse"></div>
              <div class="pulse_1"></div>
              <div class="pulse_2"></div>
            </div>
        </router-link>
</div>

  <div class="topNav">
      <ul class="menu border-bottom-solid">
         <li>
               广州
              <span @click="pullDown1=!pullDown1,pullDown2=true">
              <i v-show="pullDown1" class="icon iconfont  icon-jiantou9"></i>
              <i v-show="!pullDown1"  class="icon iconfont  icon-xiangshang_jiantou"></i>
            </span>
        </li>
        <li> 
             {{show_type}}
             <span @click="pullDown2=!pullDown2,pullDown1=true,popup_type=true">
            <i v-show="pullDown2"  class="icon iconfont  icon-jiantou9"></i>
            <i v-show="!pullDown2"  class="icon iconfont  icon-xiangshang_jiantou"></i>
          </span>
        </li>
      </ul>
  </div>


   <ul>
        <li><router-link to="/doctor_index/doctor" >我的就诊人</router-link></li>
      
    </ul>

<router-view></router-view>

<div class="popup-type"  v-show="popup_type" @click.capture="popup_type=!popup_type,pullDown2=!pullDown2"  @touchmove.prevent>
        <transition name="popup"> 
            <div class="choose" v-show="popup_type" @click.stop="popup_type=true,pullDown2=!pullDown2">
                <div class="popmenu">
                    <div  class="popmenu_item  scale-bottom-dashed"
                        v-for="(item,key,index) in doctor_list" 
                        :class="{popmenu_active:show_type==key}"
                        @click="show_type=key">
                          <div  class="left">
                           {{key}}
                          </div>
                          <div v-show="show_type==key" class="right">
                             <i class="icon iconfont icon-zhengque"></i>
                          </div>
                    </div>
               </div>        
            </div>
        </transition>  
</div>


</div>

</template>
<script type="text/ecmascript-6">
 import ScrollNew from '@/components/base/scroll'
 import { Toast } from 'mint-ui'
 import Slider from "@/components/base/slide"
 import BigImg from "../base/big_img"

export default {

data(){
  return{
    degreeId:'1',
    popup_type:false,
    pullDown1:true,
    pullDown2:true,
    show_type:'',
    doctor_type:['口腔全科','种植专科','正畸专科',"儿童口腔科"],
    slides:[
        {
           src: require('../../../static/img/ecoupdetail_slide1.png')
        },
        {
            src: require('../../../static/img/ecoupdetail_slide1.png') 
        }  ],
    showImg:false,
    imgSrc:'',
    autoplay: 2000,
    recognized_like:[],
    doctor_list:null,
   }
},

created(){
  let arr=[]
  for(let i in this.doctor_list){
    (function(arg){ 
      arr.push(i)
      })(i)
  }
  this.show_type=arr[0]
   //console.log(this.doctor_list.口腔全科[0])
 },

 methods:{
  get_data(){
    //获取个人信息
    this.$http.get(this.GLOBAL.BASE_URL+'/huananhospital/listWcDoctorByDegreeId?degreeId='+this.degreeId).then((res)=>{
      console.log(res.data.data)
      this.doctor_list=res.data.data

  /*    if(res.data.errorCode==0){
       this.personDate=res.data.data
       this.GLOBAL.USER_NAME=this.personDate.nickname
      }else{
          Toast('数据请求错误')
      }
*/


      
    })



    },

  clickImg(e) {
         this.showing = true;
　　　　　　this.showImg = true;
　　　　　　// 获取当前图片地址
　　　　　　this.imgSrc = e.currentTarget.src;
          console.log(this.imgSrc )
　　　　},
　　 viewImg(){
          this.showing = false;
　　　　　　this.showImg = false;
　　},
},

components: {
    ScrollNew,
    Slider,
    BigImg
   },

mounted () {

  this.get_data();
  sessionStorage.setItem("doctor_list",JSON.stringify(this.doctor_list));
 }
}
</script>

<style lang="stylus" type="text/stylus" scoped>
.wrap
  background:#F2F2F2
 @import 'swiper/dist/css/swiper.min.css'

 @keyframes scales
    0%   
        transform: scale(1)  
        opacity: 1
    
    25% 
        transform: scale(1.1) 
        opacity: 0.8
     
    50% 
        transform: scale(1.12)
        opacity: 0.5
    
    75% 
        transform: scale(1.14) 
        opacity: 0.5
  
    100% 
        transform: scale(0) 
        opacity: 0  
        

 @keyframes swinging
   0% 
     transform: rotate(0deg)
   25%
     transform: rotate(-10deg)
   50%
     transform: rotate(0deg)
   75%
     transform: rotate(10deg)
   100%
     transform: rotate(0deg)

 
  @keyframes spin
    0%   
        transform:scale(0.9) 
    
    50% 
        transform:rotate(1.1) 
     
    100% 
        transform:rotate(0.9)
    
 @keyframes halo 
 
    0%   
        transform: scale(0)  
        opacity: 0.0
    
    25% 
        transform: scale(0) 
        opacity: 0.1

    50% 
        transform: scale(0.1)
        opacity: 0.3
    
    75% 
        transform: scale(0.5) 
        opacity: 0.5
  
    100% 
        transform: scale(1) 
        opacity: 0.0  
.header
  position:relative
  .halo
    width:2px
    height:2px
    position:fixed
    border-radius:50%
    top:6rem
    /*top:2.5rem*/
    right:0.9rem
    z-index:999
    border:1px solid red
    .scale-1px:after
      border-radius:50%
      border:1px solid red
    .appoint_box
        width:74px
        height:74px
        position:absolute
        border-radius:50%
        border-sizing:content-box
        background:#0075BF
        top:0
        bottom:0
        left:-36px
        margin:auto
        z-index:999
        color:#fff
        text-align:center
       .iconfont
         font-size:0.48rem
         display:block
         padding-top:10px
         transition:all 1s ease
         animation:spin 1.5s linear infinite
       p
         height:0.3rem
         line-height:0.3rem
         font-size:0.26rem
         animation:swinging  2s ease-in-out 3s 1 alternate both
    .pulse
        width:92px
        position: absolute
        top:0
        bottom:0
        left:-51px
        margin:auto
        border-sizing:content-box
        height:92px
        border-radius:50%
        border: 6px solid #0893EA
        opacity: 0 
        animation: halo 2s ease-out infinite 
    .pulse_1
        width:78px
        position: absolute
        top:0
        bottom:0
        border-sizing:content-box
        margin:auto
        left:-39px
        height:78px
        border-radius:50%
        opacity:1
        animation:scales 1.5s ease-out infinite
        border: 1px solid rgba(8,147,234.0.6)
    .pulse_2
        width:70px
        position: absolute
        border-sizing:content-box
        top:0
        bottom:0
        left:-35px
        margin:auto
        height:70px
        border-radius:50%
        animation:scales 2s ease-out infinite
        border:1px solid rgba(8,147,234.0.4)
      
  .swiper-slide
    box-shadow: 0px 7px  5px rgba(231,227,227,1)
    height:2.8rem
    img
       min-height:2.8rem
       max-height:100%
       width:100%
       




       
.topNav
      z-index:99
      .menu
          display:flex
          height:0.75rem
          line-height:0.75rem
          flex-direction:row
          li:nth-child(1)
            border-right:1px solid #F2F2F2
          li:nth-child(2)
            border-left:1px solid #F2F2F2
          li
            flex:1
            text-align:left
            padding:0 25px
            color:#333
            font-size:0.3rem
            box-sizing:border-box
            span
              float:right
             .iconfont
               font-size:0.28rem
      
.doctor_content
   width:100%
   padding:8px
   margin:auto
   background:#F2F2F2
   overflow:hidden
   .scale-1px:after
     border-bottom:none     
  .doctor_list
    padding:0.15rem 0
    .doctor_header
       height:auto
       padding:0.15rem 0
       display:flex
       border-radius:8px 8px 0 0
       div
         display:flex
         justify-content:center
         align-items:center
       div:nth-child(1)
         width:35%
         .Avator
           height:100%
           width:1.8rem
           height:1.8rem
           border-radius:50%
           border:0.1rem solid #fff
           overflow:hidden
           img
            width:100%
            height:100%
       div:nth-child(2)
          width:65%
          padding-left:0.2rem
          padding-right:0.2rem
          justify-content:left
          box-sizing:border-box
          flex-direction:column
          b
            font-weight:normal
            color:#333
            display:inline-block
            padding-right:3px
          strong
            color:black
          p
            color:#333
            text-align:left
            width:100%
            font-size:0.24rem
            //border:1px solid red
          .p1
             font-size:0.29rem
             text-alian:left
             color:#0379C4
             padding-right:0.5rem
             box-sizing:border-box
             //color:#0095FF
             font-weight:bold
             height:0.5rem
             line-height:0.5rem
             span
               display:inline-block
               height:0.28rem
               line-height:0.28rem
               margin-left:5px
               //border:1px solid red
               .iconfont 
                 color:#F4E336
                 //border:1px solid red
                 font-size:0.24rem
          .P3
            dispaly:flex
            width:100%
            height:0.45rem
            line-height:0.45rem
            
          .p6
            width:40%
            //color:#C5C6C9
            color:#0379C4
          .p5
               height:0.4rem
               line-height:0.4rem
               width:60%
               dispaly:inline-block
          .p4
            .scale-1px:after
              border-radius:10px
              border:1px solid #ddd
            span
              display:inline-block
              padding:0 3px
              z-index:2
              box-sizing:content-box
              margin-right:5px
              margin-top:5px
              border-radius:8px
            //box-shadow: 0px 2px  8px rgba(255,255,255,0.3)  
   .doctor_desc
        z-index:2
        padding-top:5px
        overflow: hidden
        height:0.7rem
        line-height:0.7rem
        /*position:relative*/
        display:flex
        .scrolls-view
          width:90%
          text-indent:15px
        .more-btn
          width:10%
          button
            padding:0 15px 
            position:absolute
            top:0
            bottom:0
            margin:auto
            right:0
            font-size:0.26rem
            color:#0490D3   
      .nav
         height:1.1rem
         color:both
         z-index:2
         padding:5px 0
         box-shadow: 0px 5px  14px rgba(231,227,227,0.5)
         ul
           width:6rem
           height:100%
           margin:0 auto
           li
             width:20%
             height:100%
             float:left
             text-align:center
             display:flex
             a
              margin:auto
              width:100%
              display:block
              .iconfont
                font-size:0.55rem 
              p
                font-size:0.23rem
                color:#333
           li:nth-child(1) .iconfont
               color:#E92A2D
           li:nth-child(2) .iconfont
               color:#F9B647
           li:nth-child(3) .iconfont
               color:#4C59A3
           li:nth-child(4) .iconfont
               color:#E32485 
           li:nth-child(5) .iconfont
               color:#31B2E1
 .popup-type
    position:fixed
    width:6.4rem
    height:100%
    z-index:999999999
    background:rgba(0,0,0,0.65)
    top:0
    .choose
        width:60%
        max-height:90%
        background:#fff
        z-index:9999999999
        position:fixed
        top:0
        bottom:0
        right:0
        left:0
        margin:auto 
        overflow:auto
        .popmenu
           width:100%
           color:#666
           background:#fff
           padding-top:10px
           .popmenu_item
              display:flex
              height:0.8rem
              z-index:2
              line-height:0.8rem
              font-size:0.29rem
              div:nth-child(1)
                flex:1
                text-align:left
                text-indent:0.2rem
              div:nth-child(2)
                width:0.8rem
           .popmenu_active
              background:#F7F7F9
              transition:all 1s ease
              div:nth-child(1)
                color:#F58220
              .iconfont
                color:#F58220
        .tip
           text-align:center
           margin-top:10px
           font-size:0.27rem
           color:#F58424
           height:0.45rem
           line-height:0.45rem
        .AddBtn
          text-align:center
          margin-top:15px
          height:0.8rem
          line-height:0.8rem
          font-size:0.27rem
          color:#29AFE0

</style>



